///Page banner
.page-banner-wrapper
{
    display: block;
    height: calc(100vw/3);
    overflow: hidden;
    position: relative;
}

.page-banner-container
{
    position: fixed;
    width: 100vw;
    top:0;
    z-index: -1;
}
.page-img-wrapper{
    width: 100%;
    padding-top: 50%;
    position: relative;
    overflow: hidden;

    .page-banner-img
    {
        position: absolute;
        top:50%; transform: translateY(-50%) scale(1);;
        left:0; right:0;
        width: 100%;
        filter: brightness(.5);
        animation-name: page-bg-anim;
        animation-duration: 20s;
        animation-iteration-count: infinite;
    }
}
@keyframes page-bg-anim
{
    0%{transform: translateY(-50%) scale(1);}
    50%{transform: translateY(-50%) scale(1.05);}
    100%{transform: translateY(-50%) scale(1);}
}

.page-banner-header
{
    padding:0; margin:0;
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left:0;
    right:0;
    font-size: 3rem;
    color:#fff;
}
